@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
.container {
    font-size: 25px;
    color: white;
    padding: 20px;
    border-radius: 5px;
    background-color: white;
    box-shadow: 0 0 10px #000000;
}

.result-box {
    border-radius: 5px;
    width: 90%;
    padding: 15px;
    margin: 0 auto;
    margin-top: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.solution {
    background-color: rgb(21, 89, 235);
}

.delta {
    background-color: rgb(235, 21, 224);
}

.xvertex {
    background-color: rgb(139, 21, 235);
}

.yvertex {
    background-color: rgb(235, 21, 92);
}

.concavity {
    background-color: #440bca;
}
@media only screen and (max-width: 1100px) {
  .float-element {
    margin-top: 25px;
  }
}

@media only screen and (min-width: 1100px) {
  .float-element {
    float: left;
    width: 500px;
  }
}

h1 {
  font-size: 45px;
  text-align: center;
  color: black;
}

form {
  text-align: center;
  background-color: white;
  border-radius: 5px;
  padding: 25px;
  box-shadow: 0 0 10px #000000;
}

@media only screen and (min-width: 1100px) {
  form {
    margin-right: 25px;
  }
}

label {
  font-weight: bold;
  display: block;
  color: rgb(88, 88, 88);
  margin-top: 25px;
  margin-bottom: 10px;
  text-align: left;
  margin-left: 3%;
}

input {
  font-size: 20px;
  width: 90%;
  height: 15px;
  border-radius: 5px;
  border: none;
  background-color: rgb(230, 230, 230);
  color: rgb(85, 85, 85);
  padding: 15px;
}

button {
  font-size: 18px;
  width: 80px;
  padding: 10px;
  background-color: #591ce6;
  color: white;
  border-radius: 5px;
  border: none;
  margin: 0 auto;
  margin-top: 25px;
  display: block;
  cursor: pointer;
}

button:hover {
  background-color: #7c44ff;
}

button:active {
  background-color: #3609a0;
}

input:focus,
button:focus {
  outline: none;
}

/* hide the spin button in the input */

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

hr {
  border-top: 1px solid #d8d8d8;
  margin-top: 25px;
}
* {
  font-family: "Mulish", sans-serif;
  padding: 0;
  margin: 0;
}

body {
  padding: 20px;
  min-height: 100vh;
  box-sizing: border-box;
  background-color: #2b1c4e;
  display: flex;
  align-items: center;
  justify-content: center;
}

